#emergency-intro {
  padding: 50px 0;
  color: var(--white-normal);
  background: url(../../img/Emergency/bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  z-index: 1;
}

#emergency-intro:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
    to top right,
    rgba(1, 144, 176, 0.7),
    rgba(0, 181, 221, 0.7)
  );
  z-index: -1;
}

#emergency-intro h2 {
  font-size: 36px;
  font-weight: 700;
}

#emergency-intro span {
  font-size: 56px;
  font-weight: 900;
}

@media (max-width: 800px) {
  #emergency-intro h2 {
    font-size: 26px;
    font-weight: 600;
  }
  #emergency-intro span {
    font-size: 46px;
    font-weight: 800;
  }
}

@media (max-width: 414px) {
  #emergency-intro h2 {
    font-size: 20px;
    font-weight: 550;
  }
  #emergency-intro span {
    font-size: 30px;
    font-weight: 800;
  }
  #emergency-intro p {
    font-size: 12px;
  }
}

@media (max-width: 360px) {
  #emergency-intro h2 {
    font-size: 18px;
    font-weight: 550;
  }
  #emergency-intro span {
    font-size: 28px;
    font-weight: 800;
  }
  #emergency-intro p {
    font-size: 10px;
  }
}